<template>
    <div v-if="isShowList === 'SJXA'" class="sjxa">
      <div class="des">
        {{des}}
      </div>
      <div class="goods-list">
        <div
          class="goods-item"
          v-for="(item,index) in goodsList"
        >
          <div class="goods-item-inner">
            <img :src="item.icon" >
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    props:{
      isShowList:{
        type:String,
      }
    },
    data(){
      return {
        des:'',
        goodsList:[
          {
            title:'',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/kr.png')
          },
          {
            title:'FAB ',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/fab.png')
          },
          {
            title:'',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/bq.png')
          },
          {
            title:'CeraVe',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/slf.png')
          },
          {
            title:'',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/yz.png')
          },
          {
            title:'',
            description:'',
            icon:require('../../assets/typeicon/repairicon/sjxa/cy.png')
          },
        ]
      }
    }
  }
</script>

<style scoped>
  /*.goods-list{
    float: left;
    overflow: hidden;
  }
  .goods-item{
    float: left;
    width: 300px;
    text-align: center;
    padding: 20px 0;
  }*/
</style>



