<template>
  <div v-if="isShowList === 'XGG'" class="Arbutin">
    <div class="des">
      {{des}}
    </div>
    <div class="goods-list">
      <div
        class="goods-item"
        v-for="(item,index) in goodsList"
      >
        <div class="goods-item-inner">
          <img :src="item.icon" >
          <h2>{{item.title}}</h2>
          <p>{{item.description}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props:{
      isShowList:{
        type:String,
      }
    },
    data(){
      return {
        des:'10',
        goodsList:[
          {
            title:'',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/xgg/cazm.png')
          },
          {
            title:' ',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/xgg/wnn.png')
          },
          {
            title:'Tunemakers',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/xgg/tunemakers.png')
          },

          {
            title:'The Ordinary 2% ',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/xgg/theordinary.png')
          },
        ]
      }
    }
  }
</script>

<style scoped>

</style>



