<template>
    <div v-if="isShowList === 'YXA'" class="yxa"><!-- -->
      <div class="des">
        {{des}}
      </div>
      <div class="goods-list">
        <div
          class="goods-item"
          v-for="(item,index) in goodsList"
        >
          <div class="goods-item-inner">
            <img :src="item.icon" >
            <h2>{{item.title}}</h2>
            <p>{{item.description}}</p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
    props:{
      isShowList:{
        type:String,
      }
    },
    data(){
      return {
        des:'',
        goodsList:[
          {
            title:'SK2',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/yxa/sk2.png')
          },
          {
            title:'OLAY ',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/yxa/olayprox.png')
          },
          {
            title:'OLAY',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/yxa/olay.png')
          },

          {
            title:'The Ordinary 10% ',
            description:'',
            icon:require('../../assets/typeicon/whiteicon/yxa/theordinary.png')
          },
        ]
      }
    }
  }
</script>

<style scoped>
  /*.goods-list{
    float: left;
    overflow: hidden;
  }
  .goods-item{
    float: left;
    width: 300px;
    text-align: center;
    padding: 20px 0;
  }*/
</style>



